ul.nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    top: 0;
    height: 100%;
    width: 100%;
}

li.nav {
    float: left;
}

li.nav a.nav {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

li.nav a.nav:hover:not(.active) {
    background-color: #111;
}

.active {
    background-color: #4CAF50;
}

li.pushedRight {
    float: right;
}